@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: rgba(0, 0, 0, 0.01);
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16px;
}

:root {
  --brown: rgba(140, 11, 54, 1);
  --lightblue: rgba(75, 98, 168, 1);
  --lightgreen: rgba(96, 191, 52, 1);
  --voilet: rgba(86, 14, 105, 1);
  --sandal: rgba(240, 205, 86, 1);
}

#root {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}

nav {
  height: 55px;
  width: 100%;
  display: flex;
  padding: 0 50px;
  position: fixed;
  z-index: 9999;
  align-items: center;
  justify-content: space-between;
  background: #ffffff;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
}
.sidebar__container ul {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}
.sidebar__container ul a:hover {
  background-color: #eee;
}
.sidebar__container ul a {
  width: 100%;
  color: #111;
  text-decoration: none;
  text-align: center;
  padding: 15px 0;
}
nav ul {
  display: flex;
  justify-content: space-evenly;
  min-width: 250px;
  margin-right: auto;
  margin-left: 20px;
}
nav ul a {
  padding: 5px 0;

  border-bottom: 3px solid transparent;
}
nav ul .active {
  font-weight: 600;
  box-sizing: content-box;
  border-bottom: 3px solid rgba(75, 98, 168, 1);
  border-bottom: 3px solid var(--lightblue);
}
nav a {
  color: rgba(86, 14, 105, 1);
  color: var(--voilet);
  height: 60%;
  text-decoration: none;
}
nav .logo {
  height: 100%;
}
nav .nav-button {
  outline: none;
  padding: 5px 15px;
  background: rgba(86, 14, 105, 1);
  background: var(--voilet);
  color: white;
  height: unset;
  margin: 0 5px;
  text-decoration: none;
  border: 1px solid rgba(86, 14, 105, 1);
  border: 1px solid var(--voilet);
  border-radius: 5px;
  cursor: pointer;
  font-family: "Source Sans Pro", sans-serif;
  transition: 0.2s;
}

nav .nav-button:hover {
  color: rgba(86, 14, 105, 1);
  color: var(--voilet);
  background: white;
}

nav .mobile-menu:hover {
  background-color: #eee;
}
nav .mobile-menu {
  display: none;
  padding: 10px;
  cursor: pointer;
  border-radius: 50%;
  color: rgba(86, 14, 105, 1);
  color: var(--voilet);
}
.hidden {
  visibility: hidden !important;
}
.sidebar {
  position: absolute;
  top: 0;
  z-index: 999999;
  left: 0;
  display: none;
  height: 100%;
  transition: display 0.3s;
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
}
.sidebar.open {
  display: block;
}
.sidebar__container.open {
  transform: translateX(0%);
}
.sidebar__container {
  width: 300px;
  background-color: white;
  height: 100%;
  position: absolute;
  padding: 10px 0;
  transform: translateX(-100%);
  transition: transform 0.5s;
  transition-delay: 0.3s;
}
.close-button {
  position: absolute;
  top: 10px;
  right: 20px;
  color: white;
  cursor: pointer;
  font-size: 2em;
}
.sidebar__button {
  outline: none;
  padding: 15px 0;
  font-size: 1.1em;
  font-weight: 600;
  background: rgba(86, 14, 105, 1);
  background: var(--voilet);
  color: white;
  display: block;
  text-align: center;
  width: 90%;
  margin: 0 5%;
  margin-bottom: 10px;
  text-decoration: none;
  border: 1px solid rgba(86, 14, 105, 1);
  border: 1px solid var(--voilet);
  border-radius: 5px;
  cursor: pointer;
  font-family: "Source Sans Pro", sans-serif;
  transition: 0.2s;
}
.sidebar__button:hover {
  color: rgba(86, 14, 105, 1);
  color: var(--voilet);
  background-color: white;
}
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background-color: #ddd;
}
::-webkit-scrollbar-thumb {
  background-color: #bbb;
  border-radius: 10px;
}

@media only screen and (max-width: 550px) {
  nav {
    padding: 0 15px;
  }
}
@media only screen and (max-width: 700px) {
  nav .nav-button,
  nav ul {
    display: none;
  }
  nav .mobile-menu {
    display: block;
  }
  nav a {
    flex: 1 1;
    display: flex;
  }
  nav a img {
    margin: 0 auto;
  }
}
.alert{
  width: 80%;
  margin: 0 auto;
  background-color: orangered;
  padding: 20px 40px;
  color: white;
  border-radius: 10px;
  margin-top: 65px;
}
.alert strong{
  color: yellow;
}
.wrongEmailContainer{
  width: 80%;
  text-align: center;
