}
.wrongEmailContainer .nav-button {
  outline: none;
  padding: 5px 15px;
  background: rgba(86, 14, 105, 1);
  background: var(--voilet);
  color: white;
  margin: 20px auto;
  text-decoration: none;
  border: 1px solid rgba(86, 14, 105, 1);
  border: 1px solid var(--voilet);
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.2em;
  font-family: "Source Sans Pro", sans-serif;
  transition: 0.2s;
}

.wrongEmailContainer .nav-button:hover {
  color: rgba(86, 14, 105, 1);
  color: var(--voilet);
  background: white;
}

