  overflow-y: auto;
  overflow-x: hidden;
}
.row {
  display: flex;
}
.col {
  display: flex;
  width: 50%;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.hero-banner {
  width: 60%;
  height: 100%;
}
.signin-banner h1 {
  color: var(--voilet);
  font-size: 1.5em;
}
.google-sign-button {
  width: 200px;
  height: 45px;
  margin: 10px 0;
  cursor: pointer;
  transition: 0.2s;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  position: relative;
}
.google-sign-button img {
  position: absolute;
  width: 100%;
