  color: var(--lightgreen);
  margin: 0 10px;
  font-size: 1.5em;
}
.banner .quote {
  width: 60%;
  margin-bottom: 10px;
  font-size: 1.3em;
  color: var(--lightblue);
}
.fa-quote-right {
  align-self: flex-end;
}
.quote-author {
  align-items: center;
}
.quote-author img {
  width: 35px;
  height: 35px;
  margin-right: 5px;
  border-radius: 50%;
}
.quote-author h3::before {
  content: "- ";
}

